import { Component, OnInit } from '@angular/core';
import {
  trigger,
  animate,
  transition,
  state,
  style
} from '@angular/animations';

import { GeneticModule } from './genetic_module';
import { GeneticElement } from './genetic_element';
import { Base } from './base';

